import React from 'react';
import {NavLink} from 'react-router-dom';


const Common = (props) => {
    return(
        <>
        <section id="header" className="d-flex align-items-center">
        <div className="container-fluid">
            <div className="row">
            <div className="col-10 mx-auto">
            <div className="row">
            <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justified-content-center flex-column">
            <h1>Grow your business with <strong className="badge bg-primary">Perfect Technologies</strong></h1>
            <h2 classname="my-3">{props.name}</h2>
            <div className="mt-3"><NavLink to ={props.visit} className="btn btn-success">{props.btname}</NavLink></div>

            </div>
            <div className="col-md-6 order-1 order-lg-2 header-img d-flex justified-content-center flex-column">
                <img src={props.imgsrc} className="img-fluid animated" alt="" />
            </div>
            </div>
            </div></div></div>
        </section>
        </>
    )
}

export default Common;